import React from "react";
import styled from 'styled-components';
import homeBackground from "../images/logo.jpg";
import DefaultLayout from "../components/layout/DefaultLayout";
import HomeBackground from "../components/common/HomeBackground";
import MaskDark from "../components/common/MaskDark";
import Title from "../components/common/Title";
import FontFamily from "../components/common/FontFamily";
import Middle from "../components/common/Middle";
import OpenACard from "../components/common/OpenACard";

const ContentSize = styled.div`
	width: 100%;
`;
const OneHalf = styled.div`
    margin: 10px 185px 0px 185px;
    width: 75%;
    display: flex;
`;
const Column = styled.div`
    width: 50%;
    height: 100%;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
`;

const ExperiencePage = () => {
    const items = [
        { id: '1', title: '第一次學習程式語言', subtitle: 'C++', subcontent: '寫內容哦', subImg: homeBackground },
        { id: '2', title: '學習各種程式語言及物件導向', subtitle: 'Java、Python、Html、CSS、JavaScript' },
        { id: '3', title: '參加參訪', subtitle: '系上舉辦的"麥克斯兄弟"參訪' },
        { id: '4', title: '接觸教學助理(TA)', subtitle: '物件導向TA', subcontent: (
            <>
                我覺得這次擔任TA最大的收穫莫過於把自己學到的東西再用自己的話講出來，<br />
                想當初自己在理解這些東西的時候也是一個頭兩個大，通過助教的講解，<br />
                讓學生們可以更好的了解上課所學的東西。另外，我也在這學期中慢慢地勇於嘗試上台講話，<br />
                以前的我在台上根本不敢講話，透過這次機會讓我比較不害怕上台，但還是有很大的進步空間!!!!
            </>
            )
        },
        { id: '5', title: '擔任教學助理', subtitle: '資料結構TA', subcontent: (
            <>
                在這學期我擔任了資料結構的TA，由於有了之前當物件TA的經驗，<br />
                這學期我想換個方式教導學弟妹，在課堂上先講解一次課堂內容，<br />
                然後再進行實戰，實戰的過程中講解每行code在做什麼，<br />
                這樣可以讓學生們更好的了解上課所學的東西~~~
            </>
            )
        },
        { id: '6', title: '製作畢業專題', subtitle: '畢業專題 start' },
        { id: '7', title: '擔任教學助理', subtitle: '資料庫TA' },
        { id: '8', title: '擔任教學助理leader', subtitle: '資料結構TA leader' },
        { id: '9', title: '發表畢業專題', subtitle: '畢業專題 end' },
        { id: '10', title: '擔任教學助理leader', subtitle: '資料庫TA leader' },
        { id: '11', title: '企業實習', subtitle: '...' },
        { id: '12', title: '自學React', subtitle: '...' },
    ];
    return (
        <DefaultLayout>
            <HomeBackground url={homeBackground}>
                <MaskDark>
                    <Middle>Experience</Middle>
                </MaskDark>
            </HomeBackground>
            <ContentSize>
                <FontFamily>
                    <Title>大學一年級</Title>
                    <OneHalf>
                        <Column><OpenACard items={[items[0]]} /></Column>
                        <Column><OpenACard items={[items[1]]} /></Column>
                    </OneHalf>
                    <Title>大學二年級</Title>
                    <OneHalf>
                        <Column><OpenACard items={[items[2]]} /></Column>
                        <Column><OpenACard items={[items[3]]} /></Column>
                    </OneHalf>
                    <Title>大學三年級</Title>
                    <OneHalf>
                        <Column><OpenACard items={[items[4]]} /></Column>
                        <Column><OpenACard items={[items[5]]} /></Column>
                    </OneHalf>
                    <OneHalf>
                        <Column><OpenACard items={[items[6]]} /></Column>
                        <Column></Column>
                    </OneHalf>
                    <Title>大學四年級</Title>
                    <OneHalf>
                        <Column><OpenACard items={[items[7]]} /></Column>
                        <Column><OpenACard items={[items[8]]} /></Column>
                    </OneHalf>
                    <OneHalf>
                        <Column><OpenACard items={[items[9]]} /></Column>
                        <Column><OpenACard items={[items[10]]} /></Column>
                    </OneHalf>
                    <OneHalf>
                        <Column><OpenACard items={[items[11]]} /></Column>
                        <Column></Column>
                    </OneHalf>
                </FontFamily>
            </ContentSize>
        </DefaultLayout>
    )
};

export default ExperiencePage;